.studio-title {
  & h2 {
    font-size: 1.6rem;
  }
}

.studio-photo {
  position: relative;
  background-image: url('../../img/studio.jpg');
  margin-bottom: 35px;
  &::before {
    content: url('../../img/icons/like.png');
    position: absolute;
    transform: scale(0.36);
    left: -10px;
    top: -80px;
  }
  &::after {
    content: url('../../img/icons/love.png');
    position: absolute;
    transform: scale(0.36);
    right: 15px;
    bottom: -90px;
  }
}
