@import '../../constants.scss';

.contact-title {
  & h2 {
    font-size: 1.5rem;
  }
}

.contact-location-container {
  width: 100%;
  background-color: $contrast-blue;
  position: relative;
  & .contact-location {
    padding: 25px;
    &::before {
      display: inline-block;
      content: '';
      width: 25px;
      height: 35px;
      background-image: url('../../img/icons/location-icon.png');
      background-size: 100% 100%;
    }
  }
}

.bottom-line-contact {
  background-size: 300%;
}

.contact-whatsapp {
  width: 70%;
}

.contact-whatsapp-title {
  position: relative;
  font-size: 1.8rem;
  padding-top: 110px;
  &::before {
    content: url('../../img/icons/whats-icon.png');
    position: absolute;
    transform: scale(0.4) translateX(30px);
    top: -10px;
  }
}

.contact-phone-number {
  font-size: 1.3rem !important;
  display: inline-block;
  padding: 5px 0 10px 35px !important;
  &::before {
    transform: scale(0.6) !important;
    top: -8px !important;
  }
}

.contact-send-whatsapp-button {
  display: inline-block;
  background-color: #f5bddf;
  color: white;
  font-size: 0.9rem;
  margin: 20px;
  padding: 2px 10px;
}
