@import './constants.scss';

.cursive {
  font-family: 'Charline', cursive;
}

.site-container {
  max-width: 1440px;
  padding: 0;
}

.hide-mobile {
  display: none;
}

.hide-desktop {
  display: block;
}

.bottom-line {
  width: 90%;
  margin: auto;
  height: 15px;
  background-repeat: no-repeat;
  background-position: center center;
}

.bottom-line-white {
  background-image: url('./img/visuals/dots-white.png');
}

.bottom-line-blue {
  background-image: url('./img/visuals/dots.png');
}

.blue-title {
  margin: 5px 0;
  height: 81px;
  & h2 {
    line-height: 81px;
    color: $contrast-blue;
  }
}

.section-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.section-photo {
  width: 100%;
  height: 260px;
  background-position: 20% center;
  background-size: cover;
  background-repeat: no-repeat;
}

.section-quote {
  padding: 20px 25px 30px 25px;
  font-size: 1.4rem;
}

.author {
  font-family: $font-default;
  display: block;
  width: auto;
  font-size: 1rem;
  line-height: 10px;
  position: absolute;
  bottom: -10px;
  right: 0;
}

@media (min-width: 1024px) {
  .hide-mobile {
    display: block;
  }

  .hide-desktop {
    display: none;
  }
}
